<template>
  <div class="progress-container">
    <div
      class="progress"
      :class="{ scale }"
      ref="progressRef"
      @click="handleClick"
    >
      <!--  -->
      <div class="progress-bar" ref="progressBarRef" />
      <!--  -->
      <div
        class="progress-btn"
        ref="progressBtnRef"
        @mousedown="handleMouseDown"
      />
    </div>
  </div>
</template>

<script lang="ts">
import {
  defineComponent,
  PropType,
  reactive,
  ref,
  toRefs,
  watchEffect,
} from "vue";

export interface CallBack {
  (percent: number): void;
}

export default defineComponent({
  props: {
    // 
    percent: {
      type: Number as PropType<number>,
      default: 0,
    },
    // 
    percentChange: {
      type: Function as PropType<CallBack>,
      default: () => {},
    },
    // 
    scale: {
      type: Boolean as PropType<boolean>,
      default: false,
    },
  },
  setup(props) {
    const progressRef = ref<HTMLElement | null>(null);
    const progressBarRef = ref<HTMLElement | null>(null);
    const progressBtnRef = ref<HTMLElement | null>(null);

    const moveInfo = reactive({
      isStarted: false, // 
      startX: 0, // 
      left: 0, // 
    });
    // TODO: 
    const handleMouseDown = (e: MouseEvent) => {
      moveInfo.isStarted = true;
      moveInfo.startX = e.pageX;
      moveInfo.left = progressBarRef.value.clientWidth;
    };
    const handleMouseMove = (e: MouseEvent) => {
      e.preventDefault();
      if (!moveInfo.isStarted) return; // 

      const moveX: number = e.pageX - moveInfo.startX; // 
      const progressWidth: number = progressRef.value.clientWidth;
      //  0  progressWidth 
      const moveWidth = Math.min(
        Math.max(0, moveX + moveInfo.left),
        progressWidth
      );
      updateOffset(moveWidth);
    };
    const handleMouseUp = () => {
      // 
      if (moveInfo.isStarted) changePercent();
      moveInfo.isStarted = false;
    };

    //  bug
    window.onmousemove = handleMouseMove;
    window.onmouseup = handleMouseUp;

    const handleClick = (e: MouseEvent) => {
      const rect: ClientRect = progressRef.value.getBoundingClientRect();
      const moveWidth: number = e.pageX - rect.left;
      updateOffset(moveWidth);
      changePercent();
    };

    const updateOffset = (moveWidth: number) => {
      progressBarRef.value.style.width = `${moveWidth}px`;
      progressBtnRef.value.style.transform = `translateX(${moveWidth}px)`;
    };

    const changePercent = () => {
      const progressWidth = progressRef.value.clientWidth;
      const barWidth = progressBarRef.value.clientWidth;
      const currentPercent = barWidth / progressWidth;
      // @ts-ignore
      props.percentChange(currentPercent);
    };

    //  percent 
    watchEffect(() => {
      const { percent } = toRefs(props);
      if (
        percent.value >= 0 &&
        percent.value <= 1 &&
        !moveInfo.isStarted &&
        progressRef.value
      ) {
        const progressWidth = progressRef.value.clientWidth;
        const moveWidth = progressWidth * Number(percent.value);
        updateOffset(moveWidth);
      }
    });

    return {
      progressRef,
      progressBarRef,
      progressBtnRef,
      handleMouseDown,
      handleClick,
    };
  },
});
</script>

<style lang="scss" scoped>
.progress-container {
  display: flex;
  align-items: center;
  justify-content: center;

  .progress {
    position: relative;
    width: 500px;
    height: 4px;
    margin: 0 10px;
    border-radius: 4px;
    background: #e6e6e6;

    .progress-bar {
      position: absolute;
      height: 100%;
      border-radius: 4px;
      background: #ff4e4e;
    }

    .progress-btn {
      position: absolute;
      top: -2px;
      left: -2px;
      border-radius: 50%;
      background: #ff4e4e;
      cursor: pointer;
    }

    &:hover {
      .progress-btn {
        width: 8px;
        height: 8px;
      }
    }

    &.scale:hover {
      height: 6px;

      .progress-btn {
        width: 10px;
        height: 10px;
      }
    }
  }
}
</style>
