<template>
  <div class="pagin">
    <button type="button" class="prev-btn" @click="handlePrevClick">
      <i class="iconfont icon-preview-left" />
    </button>
    <ul class="pager" @click="handlePagerClick">
      <!--  -->
      <li
        v-if="totalPageCount > 0"
        class="page-number"
        :class="{ active: currentPage === 1 }"
      >
        1
      </li>
      <!--  -->
      <li v-if="showPrevMore" class="pager-more" />
      <!--  -->
      <li
        v-for="pager in pagers"
        :key="`pager${pager}`"
        class="page-number"
        :class="{ active: currentPage === pager }"
      >
        {{ pager }}
      </li>
      <!--  -->
      <li v-if="showNextMore" class="pager-more" />
      <!--  -->
      <li
        v-if="totalPageCount > 1"
        class="page-number"
        :class="{ active: currentPage === totalPageCount }"
      >
        {{ totalPageCount }}
      </li>
    </ul>
    <button type="button" class="next-btn" @click="handleNextClick">
      <i class="iconfont icon-preview-right" />
    </button>
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, PropType, ref, watchEffect } from "vue";

export default defineComponent({
  name: "pagin",
  props: {
    // 
    totalPageCount: {
      type: Number as PropType<number>,
      default: 13,
    },
    //  ...
    pagerCount: {
      type: Number as PropType<number>,
      default: 7,
    },
    // 
    currentPage: {
      type: Number as PropType<number>,
      default: 1,
    },
    onPageChange: Function,
    // 
    disabled: Boolean,
  },
  emits: ["page-change"],
  setup(props, { emit }) {
    const showPrevMore = ref<boolean>(false);
    const showNextMore = ref<boolean>(false);

    /**
     * 
     * 1.  9  9 
     * 2.  7  3 
     * 3.  3  1 
     */
    const pagers = computed(() => {
      const totalPageCount = Number(props.totalPageCount);
      const pagerCount = Number(props.pagerCount);
      const currentPage = Number(props.currentPage);
      // : 9 -> 48 -> 3.5
      const halfPagerCount = (pagerCount - 1) / 2;

      let showNextMore = false;
      let showPrevMore = false;

      // 
      if (totalPageCount > pagerCount) {
        // 
        if (currentPage < totalPageCount - halfPagerCount) {
          showNextMore = true;
        }
        //  1 
        if (currentPage > 1 + halfPagerCount) {
          showPrevMore = true;
        }
      }

      const pagerArray = [];

      if (!showPrevMore && showNextMore) {
        // 
        for (let i = 2; i < pagerCount; i++) {
          pagerArray.push(i);
        }
      } else if (showPrevMore && !showNextMore) {
        // 
        const startPage = totalPageCount - (pagerCount - 2);
        for (let i = startPage; i < totalPageCount; i++) {
          pagerArray.push(i);
        }
      } else if (showPrevMore && showNextMore) {
        // : 9, 8 -> 3
        const radius = Math.floor((pagerCount - 2) / 2);
        for (let i = currentPage - radius; i <= currentPage + radius; i++) {
          pagerArray.push(i);
        }
      } else {
        // 
        for (let i = 2; i < totalPageCount; i++) {
          pagerArray.push(i);
        }
      }

      return pagerArray;
    });

    /**
     * 
     */
    watchEffect(() => {
      const totalPageCount = Number(props.totalPageCount);
      const pagerCount = Number(props.pagerCount);
      const currentPage = Number(props.currentPage);
      const halfPagerCount = (pagerCount - 1) / 2;
      showPrevMore.value = false;
      showNextMore.value = false;
      if (totalPageCount > pagerCount) {
        if (currentPage < totalPageCount - halfPagerCount) {
          showNextMore.value = true;
        }
        if (currentPage > 1 + halfPagerCount) {
          showPrevMore.value = true;
        }
      }
    });

    /**
     * 
     */
    const handlePrevClick = () => {
      const currentPage = Number(props.currentPage);
      if (currentPage === 1) return;

      emit("page-change", currentPage - 1);
    };

    /**
     * 
     */
    const handleNextClick = () => {
      const currentPage = Number(props.currentPage);
      const totalPageCount = Number(props.totalPageCount);
      if (currentPage === totalPageCount) return;

      emit("page-change", currentPage + 1);
    };

    /**
     * 
     */
    const handlePagerClick = (event: Event) => {
      const currentPage = Number(props.currentPage);
      const target = event.target as HTMLElement;
      if (target.tagName === "ul" || props.disabled) return;

      const newPage = Number(target.textContent);
      if (newPage !== currentPage) emit("page-change", newPage);
    };

    return {
      showPrevMore,
      showNextMore,
      pagers,
      handlePrevClick,
      handleNextClick,
      handlePagerClick,
    };
  },
});
</script>

<style lang="scss" scoped>
.pagin {
  display: flex;
  align-items: center;
  justify-content: center;

  .pager {
    display: flex;
    align-items: center;

    .page-number,
    .pager-more {
      width: 30px;
      height: 30px;
      border: 1px solid #f2f2f2;
      border-radius: 4px;
    }

    .page-number {
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 0 5px;
      color: #333;
      font-size: 13px;
      cursor: pointer;

      &.active {
        color: #fff;
        background-color: $active-color;
        font-weight: bold;
      }
    }

    .pager-more {
      position: relative;

      &::after {
        content: "...";
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }
}

.prev-btn,
.next-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  border: 1px solid #f2f2f2;
  border-radius: 4px;
  background: #fff;
  margin: 0 5px;
  color: #333;
  cursor: pointer;
  outline: none;
}
</style>
